body {
	background-image: url("./images/background-image.jpg");
	background-repeat: no-repeat;
	background-attachment: fixed;
	background-position: center center;
	background-size: cover;
	color: white;
  /* padding: 10px; */
}

.login {
	box-shadow: 0 0 1rem 0 rgba(0, 0, 0, .2); 
	border-radius: 5px;
	position: relative;
	z-index: 1;
	background: inherit;
	overflow: hidden;
}

.login:before {
	content: "";
	position: absolute;
	background: inherit;
	z-index: -1;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	box-shadow: inset 0 0 2000px rgba(255, 255, 255, .5);
	filter: blur(10px);
	margin: -20px;
}

.navbar-topcss {
	margin-top: -25px;
}

.valid {
	display: none;
}

.invalid {
	color: red;
}

.invalid:before {
	position: relative;
	left: -10px;
	content: "x";
}

.errorblock {
	color: red;
	border: 1px solid red;
	border-radius: 5px;
	padding: 10px;
}

.linkbutton {
	background-color: inherit;
	border: none;
	color: black;
	padding: 10px 10px;
	text-align: center;
	text-decoration: none;
	display: inline-block;
	font-size: 20px;
	cursor: pointer;
	font-family: fantasy;
}

.linkbutton:hover {
	text-decoration: none;
	color: #ea9a00;
}

.search-bar {
	border: 0px;
	outline:0;
	box-shadow: none; 
	background: rgba(255, 255, 255, 0); 
	border-radius: 0px; 
	border-bottom: 2px solid black; 
	color: white;
}

.search-bar:hover, .search-bar:focus {
	border: 0px;
	outline:0;
	box-shadow: none; 
	background: rgba(255, 255, 255, 0); 
	border-radius: 0px; 
	border-bottom: 2px solid #ea9a00; 
	color: white;
}


@media only screen and (max-device-width : 700px) {
	.displayProductDesc {
		display: block;
		color: red;
		width: 100%;
		text-align: center;
   } 
   .itemPricingData {
	   position: static;
	   width: 100%;
   }
   .productItem {
		width: 100%;
		padding: 20px;
		display: inline-flex;
		flex-direction: column;
	}
	.productItemImage {
		float: none;
		align-items: center;
	}
 }

 @media only screen and (min-device-width : 701px) {
	.itemPricingData {
		padding: 10px;
		right: 0;
		position: absolute;
   } 
   .productItem {
		width: 100%;
		padding: 20px;
		display: inline-flex;
		flex-direction: row;
   }
	.productItemImage {
		float: left;
	}
 }
 
